import React from 'react';
import {withRouter, NavLink, Link} from 'react-router-dom';
import '../../../static/themes/superhero.css'



const Navbar = () => {

  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-primary">
      <Link className="navbar-brand" to="/">Quizzes</Link>

      <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarColor01" aria-controls="navbarColor01" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>

      <div className="collapse navbar-collapse" id="navbarColor01">
        <ul className="navbar-nav mr-auto">

          <li className="nav-item active">
            <NavLink className="nav-link" to="/">Home <span className="sr-only">(current)</span></NavLink>
          </li>

          <li className="nav-item">
            <NavLink className="nav-link" to="/about">About</NavLink>
          </li>

          <li className="nav-item dropdown">
            <a class="nav-link dropdown-toggle" data-toggle="dropdown" href="#" role="button" aria-haspopup="true" aria-expanded="false">Games</a>
            <div className="dropdown-menu">
              <Link className="dropdown-item" to="#">Minecraft</Link>
              <Link className="dropdown-item" to="#">Fortnite</Link>
              <Link className="dropdown-item" to="#">FIFA20</Link>
              <Link className="dropdown-item" to="#">CS:GO</Link>
              <Link className="dropdown-item" to="#">Chess</Link>
              <Link className="dropdown-item" to="#">Apex-Legend</Link>
            </div>
          </li>

        </ul>

        <form className="form-inline my-2 my-lg-0">
          <input className="form-control mr-sm-2" type="text" placeholder="Search" />
          <button className="btn btn-secondary my-2 my-sm-0" type="submit">Search</button>
        </form>

      </div>

    </nav>
  )
}


export default withRouter(Navbar)
