import React from 'react';
import '../../../static/themes/superhero.css';
import { Link } from 'react-router-dom';

/* Games Covers */
import mcCover from '../../../static/imgs/mc.jpg';
import fnCover from '../../../static/imgs/fn.jpg';
import fifaCover from '../../../static/imgs/fifa.jpg';
import csCover from '../../../static/imgs/csgo.jpg';
import chessCover from '../../../static/imgs/chess.jpg';
import apexCover from '../../../static/imgs/apex.jpg';


export const HomePage = () => {


  return (
    <div className="container">
      <div className="card-columns mt-5">
        
        <div className="grid">
          <figure className="effect-oscar">
            <img src={mcCover} alt="MINECRAFT"/>
            <figcaption>
              <h2><span>MineCraft</span></h2>
              <p>Test Your Minecraft Knowledge <Link to="#" className="text-muted">Here!</Link></p>
            </figcaption>			
          </figure>
        </div>

        <div className="grid">
          <figure className="effect-oscar">
            <img src={csCover} alt="CSGO"/>
            <figcaption>
              <h2><span>CS:GO</span></h2>
              <p>Test Your CS:GO Knowledge <Link to="#" className="text-muted">Here!</Link></p>
            </figcaption>			
          </figure>
        </div>

        <div className="grid">
          <figure className="effect-oscar">
            <img src={fnCover} alt="FORTNITE"/>
            <figcaption>
              <h2><span>Fortnite</span></h2>
              <p>Test Your Fortnite Knowledge <Link to="#" className="text-muted">Here!</Link></p>
            </figcaption>			
          </figure>
        </div>

        <div className="grid">
          <figure className="effect-oscar">
            <img src={chessCover} alt="CHESS"/>
            <figcaption>
              <h2><span>Chess</span></h2>
              <p>Test Your Chess Knowledge <Link to="#" className="text-muted">Here!</Link></p>
            </figcaption>			
          </figure>
        </div>

        <div className="grid">
          <figure className="effect-oscar">
            <img src={fifaCover} alt="FIFA20"/>
            <figcaption>
              <h2><span>Fifa20</span></h2>
              <p>Test Your Fifa20 Knowledge <Link to="#" className="text-muted">Here!</Link></p>
            </figcaption>			
          </figure>
        </div>

        <div className="grid">
          <figure className="effect-oscar">
            <img src={apexCover} alt="APEX-LEGEND"/>
            <figcaption>
              <h2><span>Apex-Legend</span></h2>
              <p>Test Your Apex Legend Knowledge <Link to="#" className="text-muted">Here!</Link></p>
            </figcaption>			
          </figure>
        </div>

      </div>

    </div>
  )
}